.App {
  width: 100%;
  max-width: 1196px;
  margin: 0 auto;
}

.hero {
  margin: 84px 0 48px 0;
  padding: 16px;
}

.section {
  padding-bottom: 112px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.sectionInfo {
  padding: 0 16px 16px 16px;
}

.sectionText {
  font-size: 18px;
  margin: 0;
  font-weight: 500;
}

.sectionText a, .sectionLink a {
  color: black;
}

.sectionLink {
  font-size: 18px;
  margin: 4px 0 0 0;
}

.items {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.item {
  width: 352px;
  padding: 24px 16px;
  position: relative;
  flex-shrink: 0;
}

@media only screen and (max-width: 1024px) {
  .item {
    width: 352px;
    padding: calc(2vw / 2);
  }

  .items {
    padding: calc(2vw / 2) calc(2vw / 2) calc((2vw / 2) + 200px);
    margin-bottom: -200px;
    flex-wrap: nowrap;
    overflow-x: scroll;
  }

  .section {
    padding: 0;
    overflow: hidden;
    padding-bottom: 40px !important;
  }

  .hero {
    margin: 32px 0;
    padding: 2vw !important;
  }

  .heroHeader {
    font-size: 60px !important;
  }

  .heroSubheader {
    font-size: 16px !important;
    max-width: 100% !important;
  }

  .sectionInfo {
    padding: 2vw !important;
    text-align: center;
  }
}

.heroHeader {
  margin: 0;
  font-family: "Sentinel";
  font-weight: 500;
  font-size: 120px;
}

.media {
  width: 100%;
  padding-bottom: 66.25%;
  position: relative;
  overflow: hidden;
  display: block;
}

.media img, .media video {
  position: absolute;
}

.item img, .item video {
  width: 100%;
  user-select: none;
}

.media::after {
  position: absolute;
  content: '';
  pointer-events: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid #e5e5e5;
  z-index: 1;
}

.heroSubheader {
  font-size: 20px;
  line-height: 170%;
  max-width: 70%;
}
